*{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body{
    padding: 60px;
}

input{
    font-size: 1.2rem;
    width: 100%;
    color: gray;
}

.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.card{
    align-content: center;
    border: 1px solid gray;
    border-radius: 6px;
    margin-right: 10px;
    margin-bottom: 10px;
    overflow: hidden;
}

.card p{
    text-align: center;
    padding: 5px;
}

.card img{
    max-height: 170px;
}